import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import Swal from 'sweetalert2';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Categoria } from '../models/categoria';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CategoriaService {
   url = `${environment.urlG}/categoria`

  constructor(private http: HttpClient) { }

  public categorias(): Observable<Categoria[]> {
    return this.http.get<Categoria[]>(`${this.url}` )
  }



  guardarCategoria(x: Categoria) {
    return this.http.post(`${this.url}/guardar`, x)
  }

  eliminar(id: number):Observable<Categoria> {
    return this.http.delete<Categoria>(`${this.url}/${id}`).pipe(
      catchError(e =>{
        Swal.fire('error', e.error.error)
        return throwError(e);
      })
    )
  }

  editar(x: Categoria) {
    return this.http.put(`${this.url}/${x.id}`, x)
  }

}







